import React, { useEffect, useRef, useState } from "react";
import "./Skill.css";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Canvas, useFrame } from "@react-three/fiber";
import * as THREE from "three";

const SkillsModel = () => {
  const [skillModel, setSkillModel] = useState(null);

  const skillMeshRef = useRef();
  useEffect(() => {
    new GLTFLoader().load("./windmill_stylized/scene.gltf", setSkillModel);
  }, []);

  let mixer;
  if (skillModel != null) {
    if (skillModel.animations.length) {
      mixer = new THREE.AnimationMixer(skillModel.scene);
      skillModel.animations.forEach((clip) => {
        const action = mixer.clipAction(clip);
        action.play();
      });
    }
  }

  useFrame((state, delta) => {
    if (skillModel != null) {
      skillMeshRef.current.position.y = -2.5;
      skillMeshRef.current.position.z = 0;
      skillMeshRef.current.position.x = 2.5;
      skillMeshRef.current.rotation.y = 35.5;
      mixer?.update(delta);
    }
  });
  return skillModel ? (
    <primitive
      object={skillModel.scene}
      ref={skillMeshRef}
      reciveShadow
      castShadow
      scale={[0.7, 0.7, 0.7]}
      rotation={[0, 0, 0]}
    />
  ) : null;
};
const Box = () => {
  return (
    <mesh>
      <ambientLight intensity={0.05} />
      <ambientLight />
      <pointLight intensity={0.4} position={[0, 5, 5]} />
    </mesh>
  );
};

