import React, { useEffect, useRef, useState } from "react";
import "./About.css";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Canvas, useFrame } from "@react-three/fiber";

import * as THREE from "three";
import "./About.css";

const AboutModel = () => {
  const [modelAbout, setModelAbout] = useState();

  const meshRefAbout = useRef();
  useEffect(() => {
    new GLTFLoader().load(
      "./earth_stylized_low_poly/scene.gltf",
      setModelAbout
    );
  }, []);

  useFrame(() => {
    if (modelAbout != null) {
      meshRefAbout.current.position.y = 0;
      meshRefAbout.current.position.z = 1;
      meshRefAbout.current.position.x = 2;

      meshRefAbout.current.rotation.y += 0.005;
      meshRefAbout.current.rotation.x -= 0.004;
    }
  });

  return modelAbout ? (
    <primitive
      object={modelAbout.scene}
      ref={meshRefAbout}
      reciveShadow
      castShadow
      scale={[1.25, 1.25, 1.25]}
      rotation={[0, 0, 0]}
    />
  ) : (
    console.log("not found")
  );
};
const Box = () => {
  return (
    <mesh>
      <ambientLight intensity={0.25} />
      <ambientLight />
      <pointLight intensity={2} position={[0, 5, 5]} />
    </mesh>
  );
};

