import React, { useState, useRef, useEffect } from "react";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
import { Canvas, useFrame } from "@react-three/fiber";

import "./Home.css";
import * as THREE from "three";

import TypeIt from "typeit-react";
import { FiGithub } from "react-icons/fi";
import { AiFillLinkedin } from "react-icons/ai";

const Ship = () => {
  useEffect(() => {
    new GLTFLoader().load(
      "./space_exploration_wlp_series_8/scene.gltf",
      setModel
    );
  }, []);
  const meshRef = useRef();
  const [model, setModel] = useState();

  useFrame(() => {
    if (model != null) {
      meshRef.current.position.x = 1.5;
      // meshRef.current.position.z = -15;
      // meshRef.current.position.y = 0.5;

      meshRef.current.rotation.y += 0.005;
    }
  });

  return model ? (
    <primitive
      object={model.scene}
      ref={meshRef}
      reciveShadow
      castShadow
      scale={[0.25, 0.25, 0.25]}
      rotation={[0.3, 0, 0]}
    />
  ) : (
    console.log("not found")
  );
};

const Box = () => {
  return (
    <mesh>
      <ambientLight intensity={0.05} />
      <ambientLight />
      {/* <spotLight
        intensity={0.15}
        position={[2.5, 0, 5]}
        castShadow
        lookAt={[0, 0, 0]}
      /> */}
      <pointLight intensity={0.3} position={[0, 5, 5]} />
    </mesh>
  );
};

const TextDetail = () => {
  return (
    <>
      <div className="canvas_text_container_home">
        <div className="canvas_text_container_home_name">
          <h1>Hi! I Am</h1>
          <h1>Rahil Sheikh</h1>
        </div>
        <div className="canvas_text_container_home_text">
          I'm a{" "}
          <TypeIt
            className="canvas_text_container_home_text_typeit"
            options={{ loop: true }}
            getBeforeInit={(instance) => {
              instance
                .type("Mechanical Engineer.")
                .pause(850)
                .delete(20)
                .pause(500)
                .type("Frontend developer.")
                .pause(850)
                .delete(19)
                .pause(500)
                .type("CAD/CAM Enginner.")
                .pause(850)
                .delete(19)
                .pause(500);

              return instance;
            }}
          />
          <div>
            {" "}
            I'm a Fresher Frontend Devloper from India currently learning{" "}
            <span>React.js</span> and <span>React three Fiber.</span> This site
            has been made as an experimental Project for my frontend development
            learning journey. Looking forward for a bright carrer in frontend as
            well as software development.
          </div>
        </div>
        <div className="canvas_text_container_glassmorphism"></div>
        <Icons />
      </div>
    </>
  );
};

const Icons = () => {
  return (
    <>
      <div className="canvas_icon_container">
        <div className="canvas_icon_container_Icon">
          <a
            href="https://github.com/abdulrahilsheikh"
            target="_blank"
            rel="noopener noreferrer"
            className="canvas_icon_container_Icon_content"
          >
            <FiGithub />
          </a>
          <a
            href="https://github.com/abdulrahilsheikhgithub.com/abdulrahilsheikh"
            target="_blank"
            rel="noopener noreferrer"
            className="canvas_icon_container_Icon_content"
          >
            github
          </a>
        </div>
        <div className="canvas_icon_container_Icon">
          <a
            href="https://www.linkedin.com/in/rahil-abdul-b9a1aa21b"
            target="_blank"
            rel="noopener noreferrer"
            className="canvas_icon_container_Icon_content"
          >
            <AiFillLinkedin />
          </a>
          <a
            href="https://www.linkedin.com/in/rahil-abdul-b9a1aa21b"
            target="_blank"
            rel="noopener noreferrer"
            className="canvas_icon_container_Icon_content"
          >
            linkedin
          </a>
        </div>
      </div>
    </>
  );
};
const Home = () => {
  return (
    <>
      <Canvas
        camera={{ fov: 70 }}
        onCreated={({ gl }) => {
          gl.shadowMap.enabled = true;
          gl.shadowMap.type = THREE.PCFSoftShadowMap;
        }}
      >
        <Box />
        <Ship />
      </Canvas>
      <TextDetail />
    </>
  );
};

export default Home;
